import React from "react";
import ProjectImagesContainer from "./ProjectImagesContainer";

const kirjanpitajaImgs = [{
    src: require('../../src/img/Kirjanpitaja/tositesyotto.png'),
    title: 'Tositteen syttnkym',
    alt: 'Menotositteen syttikkuna kuvakaappauksena',
  },
  {
    src: require('../../src/img/Kirjanpitaja/tablettinakyma.png'),
    title: 'Tablettinkym',
    alt: 'Menotositteen syttikkuna tablettinkymss',
  }];

function Kirjanpitaja (){
    return(
        <>
            <h2><img src={require('../../src/img//Kirjanpitaja/kirjanpitaja_logo.png')} style={{maxWidth: "90%"}}/></h2>
            <p>Kirjanpito-ohjelma pienyritykselle oli ensimminen asiakasty opinnoissa toisen vuoden alussa. Saimme hyvin vapaat kdet toteutukseen ja ptimmekin tehd mahdollisimman kyttjystvllisen ja selken version. Reilusti alle kahdessa kuukaudessa suunnittelimme ja koodasimme alusta alkaen Kirjanpitjn, jossa kaikki kuvissa nkyvt toiminnot ovat mys oikeasti toimivia. Tm toteutus tehtiin seitsemn opiskelijan voimin.</p>
            <p> Oma osuuteni projektista oli tositteen lismisosion toiminnallisuus ja tositteen tietojen tallennus tietokantaan sek ulkoasun toteuttaminen suunnitelman pohjalta. Responsiivisuudessa huomioin sovitusti nyttkoot tabletista nyttkokoon 1920 x 1080, sek huomioiden mys Windowsin suositus skaalauksen (125%), kuten ryhmn kanssa oli sovittu. Tilaaja-asiakas oli oikein tyytyvinen lopputulokseen ja saimmekin vuolaasti kehuja ptspalaverissa!</p>
            <p>Kirjanpitjss psin harjoittelemaan Scrum-kehityst kolme sprintin ajan kytten Jiraa ja Confluencea, sek vahvistamaan JavaScript, PHP, MySQL ja HTML sek CSS-taitojani. </p>
            <ProjectImagesContainer imagesToAdd={kirjanpitajaImgs} />
        </>
    )
}

export default Kirjanpitaja;