import React from "react";
import ProjectImagesContainer from "./ProjectImagesContainer";

const OnnenpyoraImgs = [{
    src: require('../../src/img/onnenpyora/aloitus.png'),
    title: 'Pelin aloitusnkym',
    alt: 'Kuvakaappaus pelin aloitusnkymst',
  },
  {
    src: require('../../src/img/onnenpyora/pelitilanne.png'),
    title: 'Kuva pelitilanteesta',
    alt: 'Kuvakaappaus osittain auenneesta tehtvst pelin aikana',
  },
  {
    src: require('../../src/img/onnenpyora/ratkaistaan.png'),
    title: 'Tehtvn ratkaisu',
    alt: 'Kuvakaappaus pelaajan ratkaistessa tehtv',
  }]

function Onnenpyora (){
    return(
        <>
            <h2 className="multicolorTextForHeading longText" style={{ fontWeight: 1200, fontSize:"3em" }}>ONNENPYR</h2>
            <p>Ensimmisen kouluvuonna opintoihin kuului Javaa. Koulukaverin ideasta kehitimme muutaman opiskelijan kesken Java-version klassisesta Onnenpyr-visailusta. Lis oppiaksemme valitsimme kyttliitymksi Java Swingin.</p>
            <p>Pelitoiminnallisuuden osilta koodasin vuoronvaihdot ja niihin liittyvt toiminnot ja osia pisteenlaskusta, sek pelaajan nimen ja pisteiden nkyvyyden pelatessa. Mys valitun konsonantin ja ostetun vokaalin etsint, ohisektorin toiminnallisuudet ja ratkaisumetodi on omaa tuotantoa.</p>
            <ProjectImagesContainer imagesToAdd={OnnenpyoraImgs} />
        </>
    )
}

export default Onnenpyora;