import React from "react";
import Article from "../Components/Article";

function MoreAboutMe (){

    return (
        <>
        <Article id="meNow" heading="Lyhyesti nykyhetkest" />
        <div className="twoArticles">
            <Article id="education" heading="Koulutus" />
            <Article id="workHistory" heading="Tyhistoria"/>
        </div>
        <Article id="studiesBeforeSchool" heading="Opiskelu ennen koulua"/>
        <Article id="future" heading="Mits nyt??"/>
        </>);
}

export default MoreAboutMe;