import React from "react";
import ProjectImagesContainer from "./ProjectImagesContainer";

const penanPuutarhaImgs = [{
    src: require('../../src/img/PenanPuutarha/tyontekijanHallinta.png'),
    title: 'CRUD-toiminnot',
    alt: 'Tyntekijn hallintasivu kuvakaappauksena',
},
{
    src: require('../../src/img/PenanPuutarha/maaritaTyo.png'),
    title: 'Tytilauksen hallinta',
    alt: 'Tytilausten hallinta taulukko kuvakaappauksena',
}];

function PenanPuutarha() {
    return (
        <>
            <h2>Penan Puutarha</h2>
            <p>Toisen vuoden lopuksi pyrytettiin kuvitteelliselle Penan Puutarhalle puutarhatiden hallintasivusto. Tarkoituksena oli perehty back-end puoleen, joten ulkoasuun ei tss liioin ole kytetty aikaa kuin sen verran, ett sivusto on helppokyttinen.</p>
            <p>Asiakas voi tilata itselleen puutarhatyn, jonka Pena voi mrt vapaalle tyntekijlle ja tyntekij nkee tylistansa, sek tykohteen paikkakunnan sn. Pena voi mys list ja poistaa tyntekijit, sek muuttaa heidn tietojaan. Saavutettavuutta harjoiteltiin tilauslomakesivulle, joka saatiinkin tyttmn AA-luokitus.</p>
            <p>Panokseni projektiin annoin luomalla Penan hallintasivun CRUD-toiminnot alusta loppuun, tynmrmistoiminnon sek siihen liittyvn automaattisen shkposti-ilmoituksen lhettmisen tyntekijlle. Loin mys tietokannan projektin kyttn, sek siihen tarvittavat schemat. Saavutettavuudessa huolehdin siit, ett tilauslomake oli kytettviss mys pelkn nppimistn avulla.</p>
            <p>Projektissa kartutettuja taitoja: Node.js, Express, Handlebars, MongoDB</p>
            <ProjectImagesContainer imagesToAdd={penanPuutarhaImgs} />
        </>
    )
}

export default PenanPuutarha;