function Fribamap() {
    return (
        <>
            <h2 id="jumpToContent" style={{ fontSize: '3em', fontWeight: 600, fontFamily: '"Baloo Paaji 2"', color: '#7cffd5', padding: 0 }}>FribaMap</h2>
            <p>Koulutukseni mobiilikehitysmoduulissa ptettiin tekaista lisosa FribaGoon React-Nativella.</p>
            <p>Projektissa osuuteni keskittyi sovelluksen navigaation toteuttamiseen, profiilinkymn luontiin ja sen vaatimien tietokantahakujen tekemiseen, sislten mys profiilin pivitysmahdollisuuden. Mys custom-markerit on minun toteuttamat, mutta lyhyen projektiajan vuoksi keskityin enemmn sen toimintaan saattamiseen, kuin ulkonkn. Nyt ne kuitenkin on helposti jatkossa vaihdettavissa parempiin merkkeihin.</p>
            <p>Tst projektista olen saanut todella hyv harjoitusta React Nativesta, Android Studiosta ja Github-haaroista</p>
            <ProjectImagesContainer imagesToAdd={fribamapImgs} />
        </>
    )
}

export default Fribamap;