import React from "react";
import ProjectImagesContainer from "./ProjectImagesContainer";

const ipaImgs = [{
    src: require('../../src/img/IPA Konevuokraamo/etusivu.png'),
    title: 'Staattisen version etusivu',
    alt: 'Kuvakaappaus konevuokraamon etusivusta',
  },
{
  src: require('../../src/img/IPA Konevuokraamo/vuokraus.png'),
  title: 'Vuokraamon yksittisen tuotteen tuotesivu',
  alt: 'Puutykalujen vuokraussivun malli',
},
  {
    src: require('../../src/img/IPA Konevuokraamo/vuokraamoEtusivuWP.png'),
    title: 'WordPress-version vuokraamon psivu',
    alt: 'Kuvakaappaus vuokrauspuolen etusivusta',
  },
  {
    src: require('../../src/img/IPA Konevuokraamo/poistotuotteetJaFooterWP.png'),
    title: 'Poistotuotteita esittelev galleria',
    alt: 'Kuvakaappaus footerin yll olevasta poistotuote sliderista',
  },
  {
    src: require('../../src/img/IPA Konevuokraamo/verkkokauppaWP.png'),
    title: 'WordPress verkkokauppa',
    alt: 'Kuvakaappaus verkkokaupan hakutoiminnosta',
  },
  {
    src: require('../../src/img/IPA Konevuokraamo/admin.png'),
    title: 'Yllpitjlle lisys-, poisto- ja muokkaus-toiminnot',
    alt: 'Kuvakaappaus yllpitjn lis-tuote osiosta',
  }];

function IpaKonevuokraamo (){
    return(
        <>
            <h2 id="jumpToContent" className="longText" style={{ fontWeight: 600, color: "#fb9d10", textShadow: "1px 1px 2px black", fontFamily: "Quantico", fontSize:"3em" }}>IPA KONEVUOKRAAMO</h2>
            <p>Koulussa ensimmisen vuonna ryhmiss tehty projektity. Ensin verkkosivu tehtiin staattisena, jonka jlkeen sama verkkosivu toteutettiin Wordpressill.</p>
            <p>Oma osuuteni projektissa oli etusivun, myytvien ja vuokrattavien tuotteiden sivujen sek yksittisen tuotteen tuotetietosivujen ulkoasut annetun suunnitelman mukaan, mys responsiivisena. Toteutin mys nille sivuille kuuluvat toiminnallisuudet.</p>
            <p>Huolehdin lisksi tuotteiden sytt-, muokkaus- ja poistotoiminnoista tietokannassa admin-puolelta ja tuotteiden hakemisesta verkkokaupan kyttn staattisessa sivustossa. Wordpressin puolella valmistin toimivan verkkokauppaosion tuotteille hakutoimintoineen. Bonuksena harjoituksen vuoksi lissin sivustolle "poistotuote"- gallerian Bootstrapill.</p>
            <p>Projektissa harjoiteltua: PHP, JavaScript, MySQL, CSS, Bootstrap-kirjasto, Figma ja Wordpress</p>
            <ProjectImagesContainer imagesToAdd={ipaImgs} />
        </>
    )
}

export default IpaKonevuokraamo;